@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto+Condensed&display=swap);
html {
  box-sizing:border-box;
}

*, *::before, *::after {
  box-sizing:inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

.page-container {
  height: 1600px;
  margin: 0;
  padding: 70px 0px 0px 0px;
  width: 100%;
}

.row-direction {
  display: flex;
  flex-direction: row;
}

#apple-icon {
  color: red;
}

#landing-section {
  align-items: center;
  background-attachment: fixed;
  background-image: url(/example-app-project/static/media/background-image.4defd6cb.jpeg);
  background-position: left top;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  -webkit-filter: grayscale(35%);
          filter: grayscale(35%);
  height: 600px;
  padding: 0;
  margin: 0;
  width: 100%;
  z-index: -1;
}

#landing-section-header {
  color: white;
  font-family: 'Pacifico', cursive;
  font-size: 80px;
  margin: 0;
}

.landing-section-subheader {
  color: yellow;
  text-align: center;
  width: 45%;
}

.by-name {
  margin-top: 0;
}

.box-builder {
  border: 1px solid transparent;
  border-radius: 5px;
  box-shadow: 0 3px 10px #9E9E9E;
  height: 75%;
  margin: 40px 0px;
  padding: 20px;
  text-align: center;
  width: 60%;
  -webkit-box-shadow: 0 3px 10px #9E9E9E;
  -moz-box-shadow: 0 3px 10px #9E9E9E;
}

.left-box {
  position: absolute;
  left: 2%;
  transition: left 1.5s ease-in-out;
}

.left-box.transition-right {
  left: 38%;
}

.right-box {
  position: absolute;
  right: 2%;
  transition: right 1.5s ease-in-out;
}

.right-box.transition-left {
  right: 38%;
}
#ltr-comp-container {
  height: 50%;
  position: relative;
  width: 100%;
}
#rtl-comp-container {
  height: 50%;
  position: relative;
  width: 100%;
}

#description-section {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 700px;
  width: 100%;
}

.subheader {
  margin-top: 40px;
}
#burger {
  color: transparent;
  font-size: 30px;
  position: absolute;
  right: -100px;
  top: -100px;
  transition: right 0.7s, color 0.5s;
  width: 50px;
  -webkit-tap-highlight-color: transparent;
}

#burger:hover {
  color: lightsteelblue;
  cursor: pointer;
}

#burger-menu {
  align-items: center;
  background-color: rgb(211, 223, 241);
  display: flex;
  flex-direction: column;
  height: 300px;
  margin: 0;
  padding: 0;
  width: 100%;
}

#links {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 0;
  padding: 0;
  width: 400px;
}

#navBarContainer {
  margin: 0;
  padding: 0;
}

.burger-menu-li {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

.burger-menu-link {
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  color: black;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  font-size: 20px;
  height: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
}

.burger-menu-link:hover {
  background-color: rgb(200, 215, 238);
  border: 1px solid rgba(0, 0, 0, 0.051);
  color: rgba(0, 0, 0, 0.585);
}

.dropdown-menu {
  position: absolute;
  right: -801px;
  top: 79px;
  transition: right 1.1s ease-in-out;
  width: 100%;
}

.dropdown-menu.open {
  display: block;
  right: 0;
  top: 79px;
}

.link {
  color: rgb(143, 158, 179);
  font-weight: bold;
  padding: 7px;
  border-color: transparent;
  transition: border-color 0.5s ease-in-out, color 0.5s ease-in-out;
}

.link:hover {
  color: rgb(131, 157, 192);
  border: 2px solid rgb(131, 157, 192);
  border-radius: 10px;
  padding: 5px;
}

li {
  list-style: none;
}

@media only screen and (min-width: 801px) {
  #burger-menu {
    display: none;
  }
}

@media only screen and (max-width: 800px) {
  #links {
    display: none;
  }

  #burger {
    color: black;
    right: 25px;
    top: 25px;
  }
}

@media only screen and (max-width: 650px) {
  .dropdown-menu {
    right: -651px;
  }
}

@media only screen and (max-width: 500px) {
  #burger {
    color: black;
    right: 22.5px;
    top: 22.5px;
  }

  .dropdown-menu {
    right: -501px;
    top: 74px;
  }

  .dropdown-menu.open {
    display: block;
    top: 74px;
  }
}

@media only screen and (max-width: 375px) {
  #burger {
    color: black;
    right: 20px;
    top: 20px;
  }

  .dropdown-menu {
    right: -376px;
    top: 70px;
  }

  .dropdown-menu.open {
    display: block;
    top: 70px;
  }
}

#headerContainer {
  align-items: center;
  background-color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.051);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 8px 10px;
  position: fixed;
  width: 100%;
  z-index: 1;
}

#companyName {
  color: lightsteelblue;
  font-family: 'Pacifico', cursive;
  font-size: 35px;
  margin: 0;
  padding: 0;
  width: 250px;
  -webkit-tap-highlight-color: transparent;
}

@media only screen and (max-width: 500px) {
  #companyName {
    font-size: 32.5px;
    width: 225px;
  }
}

@media only screen and (max-width: 375px) {
  #companyName {
    font-size: 30px;
    width: 200px;
  }
}

